import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data(){
		return{
			phone:"",
			code:"",
			shopObj:{},
			bankNO:"",
			bankName:"",
			pwd:"",
			userName:"",
			isShow:false,
			time:60,
			codeSend:false,
		}
	},
	methods:{
		isShowChange(){
			this.isShow=!this.isShow
		},
		sendCode(){
			var that=this
			if(this.codeSend){
				return;
			}
			this.codeSend=true;
			var t=setInterval(function(){
				that.time-=1;
				if(that.time==0){
					that.codeSend=false;
					that.time=60;
					clearInterval(t)
				}
			},1000)
			that.$ajax({
				url:"/sms",
				data:{
					phone:that.phone,
					type:"SMS_168820823",
				},success(d){
					console.log(d)
				}
			})
		},
		save(){
			let that=this;
			if(that.bankNO&&that.bankName&&that.pwd){
				that.$ajax({
					url:"/fund/updateBankCard",
					data:{
						bankName:that.bankName,
						mainBankNum:that.bankNO,
						cashInPassword:that.pwd,
						code:that.code,
						phone:that.phone,
						type:"SMS_168820823",
						marchantId:that.shopObj.merchantId
					},
					success(d){
						uni.showToast({
							title:"修改成功",
							icon:"none"
						})
						setTimeout(function(){
							uni.navigateBack({
								delta:1
							})
						},2000)
					}
				})
			}else{
				uni.showToast({
					title:"请填写完整的信息",
					icon:"none"
				})
			}
		}
	},
	onLoad(e){
		let that=this;
		that.userName=e.userName
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
		
	},
	onReady() {
		if(uni.getStorageSync("phone")){
			this.phone=uni.getStorageSync("phone");
			console.log(this.phone)
		}else{
			uni.showModal({
				title: '提示',
				content: '当前还没有安全手机,是否去添加',
				cancelText:"返回",
				confirmText:"去添加",
				success: function (res) {
					if (res.confirm) {
						uni.navigateTo({
							url:"/pages/system/phone"
						})
					} else if (res.cancel) {
						 uni.navigateBack({
							 delta:1
						 })
					}
				}
			});
		}
	},
}
